import React from 'react'
import Usuario, { userNull } from '../Modelos/Usuario'

interface State {
    fecha: string
    User: Usuario
    Permisos: Array<any>
    seccion: boolean
}

export const SeccionContext = React.createContext<{
    actializar: () => void,
    data: State,
    set: Function,
    logout: () => void,
    user: () => void
} | null>(null)

export class SeccionProvider extends React.Component<any, State> {
    constructor(props: any) {
        super(props)
        this.state = {
            User: userNull,
            fecha: '',
            Permisos: [],
            seccion: false
        }
    }
    render() {
        return <SeccionContext.Provider value={{
            data: this.state,
            set: this.setState,
            actializar: this.forceUpdate.bind(this),
            logout: this.logout.bind(this),
            user: this.user.bind(this)
        }}>
            {this.props.children}
        </SeccionContext.Provider>
    }
    componentDidMount() {
        this.user();
    }
    async logout() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/close', { headers: { 'Authorization': 'Bearer ' + token } });
            localStorage.removeItem('Authorization');
            await response.json();
            //cargar nuevos datos
            this.user();
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }
    async user() {
        try {
            //solicitar nuevos datos
            let token = localStorage.getItem('Authorization');
            let response = await fetch('/api/Auth/me', { headers: { 'Authorization': 'Bearer ' + token } });
            let data: Usuario = await response.json();
            //cargar nuevos datos
            console.log(data.id_usuario !== 0);

            this.setState({ User: data, Permisos: data.permisos, seccion: data.id_usuario !== 0 })
            //Actualizadar nuevos datos en cache
        } catch (err) {
            console.log(err);
        }
    }

}
export const SeccionConsumer = SeccionContext.Consumer;
